.JjOpera-r-bt{
  width: 1288px;
  height: 324px;
  display: inline-block;
  position: relative;
}
.JjOpera-r-bt{
  .JjOpera-l-top-md-r{
    position:absolute;
    top:10px;
    right: 20px;
  }
}
.JjOpera-r-top{
  width: 1304px;
  height: 536px;
}
.JjOpera-r-top-r{
  width: 598px;
  height: 534px;
  margin-left: 15px;
}
.JjOpera-r-top-l{
  width: 676px;
  height: 534px;
  .cont-list{
    padding: 0;
  }
}
.JjOpera .cont-center .cont-list ul li {
    width: 216px;
    height: 122px;
}
.JjOpera-r-top-l-b{
  width: 673px;
  height: 330px;
  margin-top: 9px;
  position: relative;
}
.JjOpera-r-top-l-b{
  .JjOpera-l-top-md-r{
    position: absolute;
    right: 20px;
    top:46px;
  }
}
.JjOpera-l-top{
  height: 422px;
}
.JjOpera-l-bt{
  height: 444px;
  z-index:2;
}
.JjOpera-r-top-l-b-l,.JjOpera-r-top-l-b-r{
    width: 50%;
    height: 294px;
    z-index:2;
}
.JjOpera-title{
  height: 34px;
}
.JjOpera-r-bt-l,.JjOpera-r-bt-m,.JjOpera-r-bt-r{
  height: 260px;
  z-index: 2;
}
.JjOpera-r-bt-l{
  width: 300px;
}
.JjOpera-r-bt-m{
  width: 400px;
}
.JjOpera-r-bt-r{
  width: 564px;
}
.JjOpera-r-top-r-b{
  height: 282px;
  z-index: 2;
}
.JjOpera-r-top-r-t{
  height: 210px;
  z-index: 2;
}
.JjOpera-l-top-bt{
  height: 294px;
  z-index: 2;
}
.JjOpera-l-top-md-l{
  margin: 0 281px 0 31px;
  span{
    color:#709DD5;
    font-size: .12rem;
  }
}
.JjOpera-l-top-md-r{
  width: 122px;
  height: 22px;
  border-radius: 20px;
  color:#85f4fb;
  font-size: .12rem;
  line-height: 22px;
  text-align: center;
  background-color:rgb(31,79,147);
}
.JjOpera-r-top-r{
  .JjOpera-l-top-md-l{
    margin-right: 230px;
  }
}
.JjOpera-center{
  display: table;
  margin: 0 auto;
  font-size: .20rem;
  .trend{
    position:relative;
    top:10px;
  }
}
.JjOpera-line{
  width: 548px;
  height: 1px;
  background: url(../images/pic_line.png);
  display: table;
  margin: 20px auto 0;
}
.JjOpera-line2,.JjOpera-line3{
  width: 1px;
  height: 250px;
  background: url(../images/pic_line2.png);
  position: absolute;
}
.JjOpera-line2{
  top:45px;
  left: 280px;
}
.JjOpera-line3{
  top:45px;
  left: 700px;
}
.JjOpera-info-tp{
  color:#D29704;
}
.JjOpera-info-bt{
  color:#0077D8;
}
.JjOpera-info{
  position: absolute;
  left: 46px;
  top:220px;
  .ic-small{
    position: relative;
    top: 10px;
  }
}
